.hero {
  background-image: linear-gradient(
    hsl(97 53% 47% / 85%),
    hsl(97 53% 47% / 85%)
  ),
    url("../img/background/barsik.jpg");
  background-repeat: no-repeat;
  background-size: 100% 63%;
}

.hero__title {
  @include oswald-reg;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  color: #ffffff;
  margin: 0;
}

.hero__slogan {
  @include oswald-reg;
  line-height: 14px;
  text-align: center;
  color: #ffffff;
  margin: 0;
}

.hero__img {
  display: block;
}
